import Vue from "vue";

const format = require('date-fns/format');

Vue.filter("creatAt", function (creatTime) {

return format(creatTime, "YYYY-MM-DD HH:mm:ss");
});
Vue.filter("updatedAt", function (creatTime) {

return format(creatTime, "YYYY-MM-DD HH:mm:ss");
});

Vue.filter("move", function (move) {
if (!move) {
return "未启动";
} else {
return "已启动";
}
});

Vue.filter("statu", function (statu) {
if (statu == 0) {
return "未付款";
} else if (statu == 1) {
return "已付款";
} else if (statu == 2) {
return "申请退款中";
} else if (statu == 3) {
return "已退款";
}
});

Vue.filter("rStatu", function (statu) {
if (statu == 0) {
return "退款中";
} else if (statu == 1) {
return "已退款";
}
})

Vue.filter("fmoney", function (num) {
let sign, cents;
num = num.toString().replace(/\$|\,/g, '');
if (isNaN(num))
num = "0";
sign = (num == (num = Math.abs(num)));
num = Math.floor(num * 100 + 0.50000000001);
cents = num % 100;
num = Math.floor(num / 100).toString();
if (cents < 10)
cents = "0" + cents;
for (var i = 0; i < Math.floor((num.length - (1 + i)) / 3); i++)
num = num.substring(0, num.length - (4 * i + 3)) + ',' +
num.substring(num.length - (4 * i + 3));
return (((sign) ? '' : '-') + num + '.' + cents);
});

Vue.filter("status", function (status) {
if (status == 0) {
return "提现失败";
} else if (status == 1) {
return "提现成功";
} else if (status == 2) {
return "提现中";
}
});



