import async from "async";
export default {
  data() {
    return {
      orderList: [],
      finishOrderList: [],
      unfinishedOrderList: []
    };
  },
  created() {
    this.axios.get(this.api + "/wx/getUser").then(res => {
      if (res.data.statu == 1) {
        this.user = res.data.user;
      } else {
        window.location = this.api + "/wx/login?goback=order";
      }
    }),
      this.axios.get(this.api + "/wx/getMyOrder").then(res => {
        // console.log(res);
        if (res.data.statu) {
          this.orderList = res.data.list;

          async.forEachOf(
            this.orderList,
            (value, key, callback) => {
              const overT = new Date(value.overTime).getTime();
              const nowT = new Date().getTime();
              if (value.move) {
                this.finishOrderList.push(value);
              } else {
                this.unfinishedOrderList.push(value);
              }
              callback();
            },
            err => {
              // console.log(err);
              return;
            }
          );
        } else {
          console.log("获取失败");
        }
      });
  },
  methods: {
    // 上拉刷新
    refresh() {
      console.log("刷新");
      setTimeout(() => {
        this.$refs.myscroller.finishPullToRefresh();
        // Indicator.open();
      }, 1000);
    },
    //下拉加载更多
    infinite(done) {
      if (this.noData) {
        setTimeout(() => {
          this.$refs.myscroller.finishInfinite(2);
          this.$refs.myscroller.resize();
        });
        return;
      }
      let self = this;
      setTimeout(() => {
        // if (this.pageNo >= this.pages) {
        //   self.noData = "没有更多数据";
        // } else {
        //   this.loadTaskList();
        // }
        self.noData = "没有更多数据";
        self.$refs.myscroller.resize();
        done();
      }, 300);
    },
    loadTaskList() {
      // this.pageNo = this.pageNo + 1;
      // Indicator.open();
      // this.axios
      //   .post(
      //     `${this.api.loadTaskList}?taskType=0&pageNo=${this.pageNo}&pageSize=${
      //       this.pageSiz
      //     }`
      //   )
      //   .then(res => {
      //     Indicator.close();
      //     if ((res.data.code = 200)) {
      //       this.list = this.list.concat(res.data.data.result.records);
      //     }
      //   });
    },
    init() {
      // this.pageNo = 1;
      // this.list = [];
      // this.noData = "";
      // this.axios
      //   .post(
      //     `${this.api.loadTaskList}?taskType=0&pageNo=${this.pageNo}&pageSize=${
      //       this.pageSiz
      //     }`
      //   )
      //   .then(res => {
      //     console.log(res);
      //     if (res.data.code == 200) {
      //       this.list = this.list.concat(res.data.data.result.records);
      //       this.pages = res.data.data.result.pages;
      //     }
      //   });
    }
  }
};



