import bannerHeader from "./header";
// import member from "./member";
// import "video.js/dist/video-js.css";
// import { videoPlayer } from "vue-video-player";

export default {
  props: {
    code: String
  },
  data() {
    return {
      home: [],
      checked: "checked",
      val: "1",
      flag: false,
      flc: false,
      isShow: false,
      user: {},
      appId: "",
      timestamp: "",
      nonceStr: "",
      signature: "",
      currentTime: "",
      orderId: " ",
      jsApiList: [],
      isFirst: true,
      investor: false
      // playerOptions: {
      //   //        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
      //   autoplay: false, //如果true,浏览器准备好时开始回放。
      //   muted: false, // 默认情况下将会消除任何音频。
      //   loop: false, // 导致视频一结束就重新开始。
      //   preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
      //   language: "zh-CN",
      //   aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
      //   fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
      //   sources: [
      //     {
      //       type: "video/mp4",
      //       src: "../../static/introduce.mp4" //你的m3u8地址（必填）
      //     }
      //   ],
      //   // poster: "poster.jpg", //你的封面地址
      //   width: document.documentElement.clientWidth,
      //   notSupportedMessage: "此视频暂无法播放，请稍后再试" //允许覆盖Video.js无法播放媒体源时显示的默认信息。
      //   //        controlBar: {
      //   //          timeDivider: true,
      //   //          durationDisplay: true,
      //   //          remainingTimeDisplay: false,
      //   //          fullscreenToggle: true  //全屏按钮
      //   //        }
      // }
    };
  },

  created() {
    // const equipmentCode = this.code;
    // alert(equipmentCode);
    // console.log(this.$route.query.code);

    //判断是否授权登录
    this.axios.get(this.api + "/wx/getUser?code=" + this.code).then(res => {
      console.log(res);
      if (res.data.statu == 1) {
        this.investor = res.data.investor;
        this.user = res.data.user;
        this.isFirst = res.data.user.firstpay;
      } else {
        window.location = this.api + "/wx/login?goback=home?code=" + this.code;
      }
    });

    //后台获取套餐
    // this.axios
    //   .get(this.api + "/wx/getMoneyPackage?code=" + this.code)
    //   .then(res => {
    //     console.log("-------------------");
    //     console.log(res);
    //     console.log("-------------------");
    //     this.home = res.data.data;

    //     //如果没有列表,首单图片隐藏
    //     // if (this.home.length < 2) {
    //     //   this.isFirst = false;
    //     // }
    //   });

    //本地套餐
    this.axios.get("/api/home").then(res => {
      this.home = res.data.data;
    });

    this.axios
      .post(this.api + "/wx/getConf", {
        path: this.api + "/#" + this.$route.path
      })
      .then(res => {
        // console.log("------------------");
        // console.log(res);
        // console.log("------------------");
        this.appId = res.data.conf.appId;
        this.timestamp = res.data.conf.timestamp;
        this.nonceStr = res.data.conf.nonceStr;
        this.signature = res.data.conf.signature;
        this.jsApiList = res.data.conf.jsApiList;
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: this.appId, // 必填，公众号的唯一标识
          timestamp: this.timestamp, // 必填，生成签名的时间戳
          nonceStr: this.nonceStr, // 必填，生成签名的随机串
          signature: this.signature, // 必填，签名，见附录1
          jsApiList: this.jsApiList // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        });
      });
  },

  // computed: {
  //   player() {
  //     return this.$refs.videoPlayer.player;
  //   }
  // },

  mounted() {},

  methods: {
    // onPlayerPlay(player) {
    //   console.log("play");
    // },
    // onPlayerPause(player) {
    //   console.log("pause");
    // },

    //点击是否同意协议
    isCheck() {
      if (this.checked == "checked") {
        this.checked = "";
        this.val = "0";
        this.flag = false;
        this.flc = true;
      } else {
        this.checked = "checked";
        this.val = "1";
        this.flag = true;
        this.flc = false;
      }
    },
    close() {
      this.flag = !this.flag;
    },
    play() {
      this.$router.push({
        path: "/video"
      });
      // this.flag = !this.flag;
    },
    loading() {
      this.isShow = true;
    },

    show() {
      this.isShow = false;
    },

    //点击调用微信支付的方法
    choosePay(item) {
      const equipmentCode = this.code;
      if (this.val != "1") {
        this.loading();
        return;
      }

      this.axios
        .post(this.api + "/wx/getPay", {
          time: item.time,
          money: item.price,
          code: equipmentCode
        })
        .then(res => {
          console.log(res);
          //如果是设备方
          if (res.data.statu == 2) {
            this.$router.push({
              path:
                "/mcMove/" + item.price + "/" + item.time + "/" + this.orderId
            });
          } else if (res.data.statu == 1) {
            //如果不是设备方
            if (res.data.statu == 1) {
              this.orderId = res.data.order_id;
              wx.chooseWXPay({
                timestamp: res.data.conf.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                nonceStr: res.data.conf.nonceStr, // 支付签名随机串，不长于 32 位
                package: res.data.conf.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                signType: res.data.conf.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                paySign: res.data.conf.paySign, // 支付签名

                // 支付成功后的回调函数
                success: res => {
                  this.$router.push({
                    path:
                      "/mcMove/" +
                      item.price +
                      "/" +
                      item.time +
                      "/" +
                      this.orderId
                  });
                }
              });
            }
          } else {
            console.log("请求失败");
            return;
            console.log(res);
          }
        });

      // if (this.investor == true) {
      //   this.$router.push({
      //     path: "/mcMove/" + item.price + "/" + item.time + "/" + this.orderId
      //   });
      // } else {
      //   this.axios
      //     .post(this.api + "/wx/getPay", {
      //       time: item.time,
      //       money: item.price,
      //       code: equipmentCode
      //     })
      //     .then(res => {
      //       if (res.data.statu == 1) {
      //         this.orderId = res.data.order_id;
      //         wx.chooseWXPay({
      //           timestamp: res.data.conf.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
      //           nonceStr: res.data.conf.nonceStr, // 支付签名随机串，不长于 32 位
      //           package: res.data.conf.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
      //           signType: res.data.conf.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
      //           paySign: res.data.conf.paySign, // 支付签名

      //           // 支付成功后的回调函数
      //           success: res => {
      //             this.$router.push({
      //               path:
      //                 "/mcMove/" +
      //                 item.price +
      //                 "/" +
      //                 item.time +
      //                 "/" +
      //                 this.orderId
      //             });
      //           }
      //         });
      //       }
      //     });
      // }
    }
  },
  components: {
    // member,
    bannerHeader
    // videoPlayer
  }
};



