import async from "async";
export default {
  data() {
    return {
      orderList: [],
      allLoaded: false,
      bottomStatus: "",
      wrapperHeight: 0,
      topStatus: "",
      translate: 0,
      moveTranslate: 0,
      bottomPullText: "上拉加载更多",
      autoFill: false,
      finishOrderList: [],
      unfinishedOrderList: []
    };
  },
  created() {
    this.axios.get(this.api+"/wx/getUser").then(res => {
      if (res.data.statu == 1) {
        this.user = res.data.user;
      } else {
        window.location = this.api+"/wx/login?goback=order";
      }
    }),
      this.axios.get(this.api+"/wx/getMyOrder").then(res => {
        console.log(res);
        if (res.data.statu) {
          this.orderList = res.data.list;
          async.forEachOf(
            this.orderList,
            (value, key, callback) => {
              const overT = new Date(value.overTime).getTime();
              const nowT = new Date().getTime();
              if (value.move) {
                this.finishOrderList.push(value);
              } else {
                this.unfinishedOrderList.push(value);
              }
              callback();
            },
            err => {
              console.log(err);
            }
          );
        } else {
          console.log("获取失败");
        }
      });
  },
  mounted() {
    this.wrapperHeight =
      document.documentElement.clientHeight -
      this.$refs.wrapper.getBoundingClientRect().top;
  },
  methods: {
    handleBottomChange(status) {
      this.bottomStatus = status;
    },
    loadBottom() {
      // setTimeout(() => {
      //   this.axios.get(this.api+"/wx/getMyOrder").then(res => {
      //     this.orderList.push(...res.data.list);
      //   });
      //   this.$refs.loadmore.onBottomLoaded();
      // }, 1500);
      this.$refs.loadmore.onBottomLoaded();
    },
    handleTopChange(status) {
      this.moveTranslate = 1;
      this.topStatus = status;
    },
    loadTop() {
      setTimeout(() => {
        this.$router.replace("/unfinishOrder2");
        this.$refs.loadmore.onTopLoaded();
      }, 1500);
    },
    toMove(item) {
      // const obj = {};
      // obj.createdAt = item.createdAt;
      // obj.updatedAt = item.updatedAt;
      // obj.payid = item.payid;
      // obj.money = item.money;
      // obj.time = item.time;
      // console.log(obj);
      // sessionStorage.setItem("_ORDER_", JSON.stringify(obj));
      if (!item.move && item.statu == 1) {
        sessionStorage.setItem("_CODE_", item.deviceId);
        // console.log('aaaa');
        this.$router.push({
          path: "/mcMove/" + item.money + "/" + item.time + "/" + item.payid
        });
      } else {
        return;
      }
    }
  }
};



