

import bannerHeader from "./header"; 
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
data() {
return {
member: {},
price: this.$route.params.price, //获取前一个页面传过来的价钱
minutes: this.$route.params.minutes, //获取前一个页面传过来的时间
currentTime: "", //当前时间,
checked: "checked",
isShow: false,
val: "1",
flag: true,
flc: false
};
},
created() {
this.$route.params.price;
this.axios.get("/api/member").then(res => {
this.member = res.data.data;
});




},
methods: {

getCurrentTime() {
this.currentTime = new Date().getTime();


this.$router.push({
path:
"/payment/" + this.price + "/" + this.minutes + "/" + this.currentTime
});



},


isCheck() {
if (this.checked == "checked") {
this.checked = "";
this.val = "0";
this.flag = !this.flag;
this.flc = !this.flc;
} else {
this.checked = "checked";
this.val = "1";
this.flag = !this.flag;
this.flc = !this.flc;
}
},
loading() {
this.isShow = !this.isShow;
},

show() {
this.isShow = !this.isShow;
},

change(data) {
console.log(data);

}
},


components: {
bannerHeader,
XSwitch,
Group,
Cell

}
};



